            <input type="tel" v-model="userInfo.phone" placeholder="请输入联系电话" :disabled="!isEditing">
          </div>
          <div class="form-group full-width">
            <label>单位/农场:</label>
            <input type="text" v-model="userInfo.organization" placeholder="请输入单位或农场名称" :disabled="!isEditing">
          </div>
        </div>
        <div class="form-actions">
