            <input type="number" v-model="systemConfig.defaultHeight" min="1" max="50" step="0.5">
          </div>
          <div class="form-group">
            <label>默认飞行速度(米/秒):</label>
            <input type="number" v-model="systemConfig.defaultSpeed" min="1" max="15" step="0.5">
          </div>
          <div class="form-group full-width">
            <label>地图默认缩放级别:</label>
            <input type="range" v-model="systemConfig.mapZoomLevel" min="8" max="18" step="1">
            <span class="range-value">{{ systemConfig.mapZoomLevel }}</span>
          </div>
        </div>
        <div class="notification-settings">
          <h3>通知设置</h3>
          <div class="checkbox-group">
            <label class="checkbox-item">
              <input type="checkbox" v-model="systemConfig.notifications.taskStatus">
              <span>任务状态变更通知</span>
            </label>
            <label class="checkbox-item">
              <input type="checkbox" v-model="systemConfig.notifications.deviceAlert">
              <span>设备异常告警</span>
            </label>
            <label class="checkbox-item">
              <input type="checkbox" v-model="systemConfig.notifications.dataReport">
              <span>数据报告提醒</span>
            </label>
            <label class="checkbox-item">
              <input type="checkbox" v-model="systemConfig.notifications.maintenance">
              <span>维护保养提醒</span>
            </label>
          </div>
        </div>
      </div>
      
      <!-- 安全设置 -->
      <div v-if="activeTab === 'security'" class="settings-section">
        <h2>安全与权限设置</h2>
        <div class="form-group">
          <label>修改密码:</label>
          <div class="password-change">
            <input type="password" v-model="securityConfig.oldPassword" placeholder="原密码">
            <input type="password" v-model="securityConfig.newPassword" placeholder="新密码">
            <input type="password" v-model="securityConfig.confirmPassword" placeholder="确认新密码">
          </div>
          <button class="btn-change-password" @click="changePassword">
            <i class="fa fa-key"></i> 修改密码
          </button>
        </div>
        <div class="permission-settings">
          <h3>操作权限</h3>
          <div class="checkbox-group">
            <label class="checkbox-item">
              <input type="checkbox" v-model="securityConfig.permissions.taskCreate" disabled>
              <span>创建任务权限</span>
            </label>
            <label class="checkbox-item">
              <input type="checkbox" v-model="securityConfig.permissions.deviceControl" disabled>
              <span>设备控制权限</span>
            </label>
            <label class="checkbox-item">
              <input type="checkbox" v-model="securityConfig.permissions.dataExport" disabled>
              <span>数据导出权限</span>
            </label>
            <label class="checkbox-item">
              <input type="checkbox" v-model="securityConfig.permissions.userManage" disabled>
              <span>用户管理权限</span>
            </label>
          </div>
          <p class="permission-note"><i class="fa fa-info-circle"></i> 部分权限需要管理员授权才能修改</p>
        </div>
      </div>
      
      <!-- 数据管理 -->
      <div v-if="activeTab === 'data'" class="settings-section">
        <h2>数据管理</h2>
        <div class="storage-info">
          <div class="storage-item">
            <div class="storage-label">存储空间使用情况</div>
            <div class="storage-bar">
              <div class="storage-progress" :style="{ width: storageUsage + '%' }"></div>
            </div>
            <div class="storage-text">{{ (storageUsed / 1024).toFixed(2) }}GB / {{ (storageTotal / 1024).toFixed(2) }}GB</div>
          </div>
        </div>
        
        <div class="data-actions">
          <h3>数据操作</h3>
          <div class="action-cards">
            <div class="action-card">
              <div class="action-icon backup">
                <i class="fa fa-database"></i>
              </div>
              <h4>备份数据</h4>
              <p>创建当前所有数据的备份</p>
              <button class="btn-action" @click="backupData">
                <i class="fa fa-download"></i> 立即备份
              </button>
            </div>
