              <p>清理3个月前的历史数据</p>
              <button class="btn-action" @click="clearHistory">
                <i class="fa fa-broom"></i> 清理数据
              </button>
            </div>
            <div class="action-card">
              <div class="action-icon export">
                <i class="fa fa-file-excel-o"></i>
              </div>
              <h4>导出报表</h4>
              <p>导出作业数据统计报表</p>
              <button class="btn-action" @click="exportReport">
                <i class="fa fa-file-text-o"></i> 导出Excel
              </button>
            </div>
          </div>
        </div>
      </div>
      
