    <!-- 消息提示 -->
    <div v-if="showMessage" class="message-toast" :class="messageType">
      {{ message }}
    </div>
  </div>
</template>

<script>
export default {
  name: "DroneSettings",
  data() {
    return {
      // 标签页管理
      tabs: [
        { id: 'user', name: '用户信息', icon: 'fa-user' },
        { id: 'system', name: '系统配置', icon: 'fa-cog' },
        { id: 'security', name: '安全设置', icon: 'fa-shield' },
        { id: 'data', name: '数据管理', icon: 'fa-database' }
      ],
      activeTab: 'user',
      
      // 用户信息
      userInfo: {
        username: 'admin',
        email: 'admin@example.com',
        realName: '管理员',
        phone: '13800138000',
        organization: '智慧农场'
      },
      isEditing: false,
      originalUserInfo: {},
      
      // 系统配置
      systemConfig: {
        language: 'zh-CN',
        refreshInterval: 30,
        defaultHeight: 10,
        defaultSpeed: 5,
        mapZoomLevel: 14,
        notifications: {
          taskStatus: true,
          deviceAlert: true,
          dataReport: true,
          maintenance: true
        }
      },
      
      // 安全设置
      securityConfig: {
        oldPassword: '',
        newPassword: '',
        confirmPassword: '',
        permissions: {
          taskCreate: true,
          deviceControl: true,
          dataExport: true,
          userManage: true
        }
      },
      
      // 数据管理
      storageUsed: 1280, // MB
      storageTotal: 5120, // MB
      
      // 消息提示
      showMessage: false,
      message: '',
      messageType: 'success'
    }
  },
  
  computed: {
    // 计算存储使用率
    storageUsage() {
      return (this.storageUsed / this.storageTotal) * 100;
    }
  },
  
  mounted() {
    // 保存原始用户信息用于取消编辑
    this.originalUserInfo = { ...this.userInfo };
    // 模拟从本地存储加载设置
    this.loadSettings();
  },
  
  methods: {
    // 加载设置
    loadSettings() {
      try {
        const savedSettings = localStorage.getItem('droneSystemSettings');
        if (savedSettings) {
          const parsed = JSON.parse(savedSettings);
          this.systemConfig = { ...this.systemConfig, ...parsed };
        }
      } catch (error) {
        console.error('加载设置失败:', error);
      }
    },
    
    // 保存用户信息
    saveUserInfo() {
      // 简单的表单验证
      if (!this.userInfo.username || !this.userInfo.email) {
        this.showToast('用户名和邮箱不能为空', 'error');
        return;
      }
      
      // 模拟保存操作
      console.log('保存用户信息:', this.userInfo);
      this.originalUserInfo = { ...this.userInfo };
      this.isEditing = false;
      this.showToast('用户信息保存成功', 'success');
    },
    
    // 取消编辑
    cancelEdit() {
      this.userInfo = { ...this.originalUserInfo };
      this.isEditing = false;
    },
    
    // 修改密码
    changePassword() {
      // 简单的密码验证
      if (!this.securityConfig.oldPassword || !this.securityConfig.newPassword) {
        this.showToast('请填写密码信息', 'error');
        return;
      }
      
      if (this.securityConfig.newPassword !== this.securityConfig.confirmPassword) {
        this.showToast('两次输入的新密码不一致', 'error');
        return;
      }
      
      // 模拟修改密码
      console.log('修改密码');
      this.securityConfig.oldPassword = '';
      this.securityConfig.newPassword = '';
      this.securityConfig.confirmPassword = '';
      this.showToast('密码修改成功', 'success');
    },
    
    // 保存所有设置
    saveAllSettings() {
      // 模拟保存操作
      const settingsToSave = { ...this.systemConfig };
      localStorage.setItem('droneSystemSettings', JSON.stringify(settingsToSave));
      console.log('保存系统设置:', settingsToSave);
      this.showToast('所有设置已保存', 'success');
    },
    
    // 恢复默认设置
    resetSettings() {
      if (confirm('确定要恢复默认设置吗？这将清除您的自定义配置。')) {
        localStorage.removeItem('droneSystemSettings');
        this.systemConfig = {
          language: 'zh-CN',
          refreshInterval: 30,
          defaultHeight: 10,
          defaultSpeed: 5,
          mapZoomLevel: 14,
          notifications: {
            taskStatus: true,
            deviceAlert: true,
            dataReport: true,
            maintenance: true
          }
        };
        this.showToast('已恢复默认设置', 'success');
      }
    },
    
    // 备份数据
    backupData() {
      this.showToast('数据备份开始，请稍候...', 'info');
      // 模拟备份操作
      setTimeout(() => {
        this.showToast('数据备份完成', 'success');
      }, 1500);
    },
    
    // 清理历史
    clearHistory() {
      if (confirm('确定要清理3个月前的历史数据吗？此操作不可恢复。')) {
        this.showToast('历史数据清理中，请稍候...', 'info');
        // 模拟清理操作
        setTimeout(() => {
          this.storageUsed = 820; // 模拟释放空间
          this.showToast('历史数据清理完成', 'success');
        }, 2000);
      }
    },
    
    // 导出报表
    exportReport() {
      this.showToast('正在生成报表，请稍候...', 'info');
      // 模拟导出操作
      setTimeout(() => {
        this.showToast('报表导出成功', 'success');
      }, 1500);
    },
    
    // 显示提示消息
    showToast(message, type = 'success') {
      this.message = message;
      this.messageType = type;
      this.showMessage = true;
      
      setTimeout(() => {
        this.showMessage = false;
      }, 3000);
    }
  }
}
</script>

